.testimonials .more-reviews {
	max-width: 1250px;
	display: flex;
	justify-content: flex-end;
}

.testimonials .more-reviews .button-text {
	font-size: 24px;
}

.testimonials .reviews-column .d-f {
	flex-direction: column;
	align-items: center;
	gap: 30px;
}

.testimonials .reviews-column .testimonial {
	transform: rotate(0deg) !important;
}

.reviews-column .d-f .col-50:not(:last-child) {
	width: 100%;
}

.reviews-column .d-f .col-50:not(:first-child),
.reviews-column .d-f .col-100 {
	display: none;
}

.testimonials .reviews-column .more-reviews {
	margin-top: 40px;
	justify-content: center;
}
