.service-card {
	font-family: "Montserrat";
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 20px;
	padding: 42px 36px 40px;
	background: #fff;
	box-shadow: 16px 16px 17px rgba(0, 0, 0, 0.25);
	border-radius: var(--button-radius);
	max-width: 448px;
	height: max-content;
}

.service-card-title {
	font-style: italic;
	font-weight: 600;
	line-height: 49px;
	text-align: center;
	width: 370px;
	padding: 28px 0;
	border-radius: var(--button-radius);
}

.service-card-description {
	color: var(--dark);
	font-weight: 400;
	font-size: 24px;
	line-height: 117.4%;
	letter-spacing: 0.05em;
}
