.contacts-row {
	display: flex;
	justify-content: flex-end;
	padding-left: 15px;
}

.contacts .contacts-map {
	text-align: right;
}

.contacts .contacts-info {
	font-family: "Montserrat";
	font-weight: 700;
	font-size: 32px;
	line-height: 40px;
	text-align: center;
	color: #fff;
	background: linear-gradient(180deg, #683a5f 0%, #845c7c 100%);
	border-radius: var(--border-radius);
	padding: 105px 115px 95px;
	width: max-content;
	box-shadow: 6px 9px 11px rgba(0, 0, 0, 0.25);
}

.contacts-line {
	background: var(--purple);
	width: 100%;
	height: 5px;
	max-width: 675px;
}

.contact-us {
	position: relative;
	line-height: 40px;
	letter-spacing: 0.11em;
	z-index: 2;
}

.contact-us::before {
	position: absolute;
	content: "";
	top: 50%;
	left: 80px;
	transform: translate(-50%, -50%);
	width: 260px;
	height: 165px;
	background: var(--orange);
	filter: blur(75px);
	z-index: -1;
}

@media only screen and (max-width: 1200px) {
	.contacts-row {
		padding-left: 15px;
		gap: 50px;
	}
	.contacts .contacts-info {
		font-size: 28px;
		padding: calc(105px * 0.6) calc(115px * 0.6) calc(95px * 0.6);
	}

	.contacts .contacts-map {
		text-align: center;
	}
}

@media only screen and (max-width: 992px) {
	.contacts-row {
		flex-direction: column;
		align-items: center;
	}

	.contacts .contacts-info {
		font-size: 30px;
		padding: calc(105px * 0.8) calc(115px * 0.8) calc(95px * 0.8);
	}
}

@media only screen and (max-width: 572px) {
	.contacts .contacts-info {
		font-size: 24px;
		padding: calc(105px * 0.6) calc(115px * 0.8) calc(95px * 0.6);
	}

	.contacts-row {
		padding-left: 0;
	}

	.contacts-map iframe {
		max-width: 100%;
	}
}
