.modal {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.5);
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 999;
}

.modal-content {
	display: flex;
	flex-direction: column;
	align-items: center;
	max-width: 1070px;
	width: 100%;
	background: var(--light-gray);
	border-radius: 70px;
	/* padding: 70px 145px 80px; */
	padding: 20px 55px 55px 35px;
}

.modal-body {
	margin-top: 56px;
}

.title-icon,
.title-icon img {
	width: 73px;
	height: 72px;
	border-radius: 0;
	object-fit: fill;
}

.modal-header {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.modal-title {
	display: flex;
	flex-direction: column;
	gap: 12px;
}

.modal-header .title {
	font-family: "Montserrat";
	font-style: italic;
	font-weight: 700;
	font-size: 32px;
	line-height: 39px;
	text-align: center;
	letter-spacing: 0.05em;
	display: flex;
	align-items: center;
	gap: 30px;
	color: var(--dark);
}

.modal-header .highlighted {
	font-family: "TT Supermolot Neue Bold";
	font-size: 48px;
	line-height: 55px;
	letter-spacing: 0.05em;
	color: var(--orange);
}

.modal-body .d-f {
	gap: 20px;
}

.modal-body .d-f + .d-f {
	margin-top: 30px;
}

.modal-input {
	font-family: "Play Regular";
	font-weight: 400;
	font-size: 20px;
	line-height: 23px;
	text-align: center;
	letter-spacing: 0.05em;
	min-width: 384px;
	width: 100%;
}

.modal-input input {
	width: 100%;
	background: transparent;
	border: 2px solid #683a5f;
	padding: 20px 0 21px 17px;
	border-radius: 15px;
	color: var(--purple);
}

.modal-input input::placeholder {
	color: var(--purple);
}

.modal-input .textarea {
	min-height: 160px;
}

/* .modal-input .textarea::placeholder {
	line-height: 20px;
	transform: translateY(-40px);
} */

.modal-footer {
	margin-top: 80px;
}

.modal-footer .button {
	position: relative;
	font-style: italic;
	font-weight: 800;
	font-size: 32px;
	line-height: 39px;
	letter-spacing: 0.025em;
	color: var(--purple);
	transition: all 0.3s ease-in-out;
	z-index: 2;
}

.modal-footer .button:hover {
	color: #9e7496;
}

.modal-footer .button:after {
	position: absolute;
	content: "";
	width: 187px;
	height: 109px;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background: #f4b16b;
	filter: blur(50px);
	z-index: -1;
}
