.footer {
	max-width: 1325px;
	padding: 0 15px;
	margin-left: auto;
	margin-right: auto;
	font-family: "Times Regular";
	color: var(--dark);
	line-height: 17px;
	letter-spacing: 0.05em;
	font-size: 15px;
}

.footer-logo {
	max-width: 305px;
}

.footer-social-media {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 30px;
}

.footer-social-media .social-icon,
.footer-social-media .social-icon img {
	min-height: 44px;
	min-width: 43px;
}

.footer-credit-cards {
	display: flex;
	align-items: center;
	gap: 15px;
}

@media only screen and (max-width: 1200px) {
	.footer-credit-cards {
		gap: 10px;
	}
}

@media only screen and (max-width: 992px) {
	.footer-credit-cards {
		display: none;
	}

	.footer .col-100 .d-f {
		justify-content: space-between;
	}

	.d-f + .d-f {
		margin-top: 30px;
	}
}
