.testimonial {
	display: flex;
	flex-direction: column;
	align-items: center;
	font-family: "Montserrat";
	font-weight: 500;
	font-style: italic;
	font-size: 18px;
	line-height: 22px;
	letter-spacing: 0.05em;
}

.testimonial .testimonial-title {
	color: var(--orange);
	margin-bottom: 10px;
}

.testimonial .testimonial-wrapper {
	padding: 35px 40px;
	border-radius: var(--border-radius);
	box-shadow: 6px 9px 11px rgba(0, 0, 0, 0.25);
}

.testimonial .testimonial-text {
	text-align: center;
	max-width: 410px;
}
